import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Home from "@/views/Home"
import Index from "@/components/Index"
import Couser from "@/components/Couser"
import Exercise from "@/components/Exercise"
import Lar from "@/components/Lar"
import My from "@/components/My"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:"/index", //默认显示index页面
    children:[  
        {
          path:"/index",  //首页路由
          name:"Index",
          component:Index,
          meta:{
            title:"登首页录"
          }
        },
        {
          path:"/couser",  //课程路由
          name:"couser",
          component:Couser,
          meta:{
            title:"课程"
          }
        },
        {
          path:"/lar",  //约课记录路由
          name:"Lar",
          component:Lar,
          meta:{
            title:"约课"
          }
        },
        {
          path:"/exercise",  //练习路由
          name:"exercise",
          component:Exercise,
          meta:{
            title:"练习"
          }
        },
        {
          path:"/my",  //我的路由
          name:"My",
          component:My,
          meta:{
            title:"我的"
          }
        },
        


    ]
  },
  {
    path:"/login",  //登录路由
    name:"login",
    component:()=>import("../views/Login/login.vue"),
    meta:{
      title:"登录"
    }
  },
  {
    path:"/setPass",  //修改密码
    name:"setPass",
    component:()=>import("../views/Login/setpass.vue"),
    meta:{
      title:"修改密码"
    }
  },
 
 
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})
router.beforeEach((to,from,next)=>{
  document.title = to.meta.title
  let token = localStorage.getItem('Token')
  if(to.name=='My'){
    if(token){
      next()
    }else{
      next('/login')
    }
  }
  next()
})

export default router
